const maxIndex = data.length - 1

function randomGame() {
  const index = Math.floor(Math.random()*maxIndex) + 1
  return data[index]
}

function GameView({game: game}) {
  const page = game[0]
  const title = game[1]
  const category = game[5]
  const url = game[7]
  const text = game[14]
  const platforms = game[15]
  const authors = game[25]
  const tags = game[27]
  const avgPlay = game[28]

  return (
    <div className="game">
      <h1>{title}</h1>
      <h4>{authors}</h4>
      <div>{text}</div>
      <div className="link"><a target="_blank" href={url}>{url}</a></div>
    </div>
  )
}

function App() {
  const [game, setGame] = React.useState(randomGame())
  return (
    <div className="App">
      <GameView game={game} />
      <button onClick={() => setGame(randomGame())}>Another!</button>
    </div>
  );
}

export default App;
